.how_work {
	margin-bottom: var(--mb-section);

	@include min {
		margin-bottom: rem(72);
	}

	@include min540 {
		margin-bottom: rem(64);
	}

	.title {
		margin-bottom: rem(96);
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			position: relative;
			width: calc((100% - 4rem) / 3);
			border-radius: rem(8);
			box-shadow: 0 0 4px rgba(10, 31, 221, .25);
			padding: rem(20);
			background-color: white;
			margin-right: rem(32);

			display: flex;
			align-items: center;

			@include min {
				width: calc((100% - 1rem) / 2);
				margin-right: rem(16);
				margin-bottom: rem(104);
			}

			@include min540 {
				width: 100%;
				margin-right: 0;
				margin-bottom: rem(57);
			}

			&__count {
				position: absolute;
				top: rem(-100);
				left: 0;
				font-size: rem(144);
				font-weight: var(--s-medium);
				color: rgba(73, 122, 240, .2);
				z-index: -1;

				@include min540 {
					font-size: rem(80);
					top: rem(-60);
				}
				
			}

			&:nth-child(3n) {
				margin-right: 0;

				@include min {
					margin-right: rem(16);
				}
			}

			@include min {
				&:nth-child(2n) {
					margin-right: 0;
				}
				&:last-child, &:nth-last-child(2) {
					margin-bottom: 0;
				}
			}

			@include min540 {
				&:nth-last-child(2) {
					margin-bottom: rem(57);
				}
				&:last-child {
					margin-bottom: 0;
				}
			}

			&:first-child,
			&:nth-child(2),
			&:nth-child(3) {
				margin-bottom: rem(104);

				@include min {
					margin-bottom: rem(104);
				}

				@include min540 {
					margin-bottom: rem(57);
				}
			}

			&__icon {
				margin-right: rem(16);
			}

			p {
				font-size: rem(20);
				font-weight: var(--s-medium);
				max-width: rem(215);
			}
		}
	}
}