.message {
	margin-bottom: rem(77);
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.title {
		margin-bottom: rem(40);
		text-align: center;
	}

	.block_wrap {
		width: rem(544);

		@include min540 {
			width: 100%;
		}

		.title {
			text-align: left;
			margin-bottom: rem(24);
			font-size: rem(20);
			color: var(--c-text);

			@include min540 {
				margin-bottom: rem(20);
			}
		}

		.field {
			margin-bottom: rem(16);
		}
	}
}