@import "var";
@import "function";

:root {
	// --var: ....
	--f-default: 'Gotham Pro';
	--c-blue: #497AF0;
	--c-blue-h: #1E5DF4;
	--c-text: #2B2B2B;

	--s-medium: 500;

	--mb-section: 120px;
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

p {
	font-size: rem(18);
	color: var(--c-text);

	@include min540 {
		font-size: rem(14);
	}
}

b, strong {
	font-weight: var(--s-medium);
}

h2,h3 {
	font-size: rem(44);

	@include min540 {
		font-size: rem(32);
	}
}

h1,h2,h3,h4,h5,h6 {
	font-weight: var(--s-medium);
	color: var(--c-text);
}

body {
	position: relative;
	font-size: rem(16);
	font-family: var(--f-default);
	// height: 100%;
	overflow-x: hidden;
	background-color: #FBFBFB;
	// -webkit-overflow-scrolling: touch;
}

.container {
	max-width: 1120px;
	margin: 0 auto;

	@include min1200 {
		max-width: 100%;
		width: calc(100% - 40px);
		// margin-left: -20px;
		// margin-right: -20px;
	}
}

.container-max {
	width: 1200px;
	margin: 0 auto;

	@include min1200 {
		max-width: 100%;
		width: calc(100% - 40px);
	}
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button-blue {
	border-radius: rem(8);
	background-color: var(--c-blue);
	color: white;
	font-size: rem(20);
	font-weight: var(--s-medium);
	padding: rem(14) rem(32);
	transition: all 200ms ease;

	&:hover {
		background-color: var(--c-blue-h);
	}
}

.button-border {
	border-radius: rem(8);
	background-color: white;
	color: var(--c-blue);
	font-size: rem(20);
	font-weight: var(--s-medium);
	padding: rem(14) rem(32);
	border: rem(1) solid var(--c-blue);

	transition: all 200ms ease;

	&:hover {
		color: var(--c-blue-h);
		border-color: var(--c-blue-h);
	}
}

.field {
	width: 100%;
	height: rem(40);
	border-radius: rem(5);
	color: var(--c-text);
	padding: 0 rem(12);
	border: rem(1) solid #BABFC3;

	font-size: rem(16);
	font-family: var(--f-default);

	@include placeholder {
		color: #868686;
	}
}

textarea.field {
	padding: rem(12);
	resize: none;
	color: var(--c-text);

	height: rem(120);

	@include placeholder {
		color: #868686;
	}
}

.field-checked {
	display: flex;
	// flex-wrap: wrap;
	// align-items: center;
	margin-bottom: rem(18);
	cursor: pointer;

	input {
		display: none;

		&:checked ~ .check {
			background-color: var(--c-blue);

			&::before {
				content: "\e903";
				font-family: 'icons';
				font-size: rem(8);
				color: white;
			}
		}
	}

	.check {
		width: rem(18);
		height: rem(18);
		flex: 0 0 rem(18);
		border-radius: rem(4);
		background-color: white;
		display: flex;
		align-items: center;
		justify-content: center;
		border: rem(1) solid var(--c-blue);
		transition: all 200ms ease;
		margin-right: rem(9);
	}

	p {
		font-size: rem(14);
		color: var(--c-text);
	}
}

.block_wrap {
	border-radius: rem(8);
	padding: rem(32);
	background-color: white;
	box-shadow: 0 0 6px rgba(73, 122, 240, .25);

	&__table {
		.title {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			padding-bottom: rem(12);
			// margin-bottom: rem(16);

			border-bottom: rem(1) solid #E5E5E5;

			span {
				font-size: rem(20);
				color: var(--c-text);
				font-weight: var(--s-medium);
			}
		}

		.item {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			padding: rem(14) 0;

			border-bottom: rem(1) solid #F5F5F5;

			span {
				font-size: rem(18);
				color: var(--c-text);
			}
		}
	}
}