.calc {
	margin-bottom: rem(70);
	.title {
		margin-bottom: rem(40);
	}

	.flex {
		
	}

	.block_wrap {
		align-self: flex-start;
		

		&:first-child {
			padding: rem(32) rem(46);
			width: rem(639);
		}

		&:last-child {
			padding: rem(32) rem(26);
			width: rem(448);

			.field {
				margin-bottom: rem(16);
			}
		}

		.title,
		.subtitle {
			font-size: rem(20);
			font-weight: var(--s-medium);
			margin-bottom: rem(24);
			color: var(--c-text);
			display: block;
			color: black;
		}

		.subtitle {
			margin-top: rem(24);
			margin-bottom: 0;

			

			&:last-child {
				margin-left: rem(100);
			}
		}

		.custom-select {
			margin-bottom: rem(16);
		}
	}
}