.advantages {
	margin-bottom: var(--mb-section);

	@include min {
		margin-bottom: rem(72);
	}

	@include min540 {
		margin-bottom: rem(64);
	}

	.title {
		margin-bottom: rem(48);

		@include min768 {
			max-width: rem(352);
		}

		@include min540 {
			max-width: rem(328);
			margin-bottom: rem(20);
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 4rem) / 3);
			border-radius: rem(8);
			box-shadow: 0 0 4px rgba(10, 31, 221, .25);
			padding: rem(32) rem(20);

			margin-right: rem(32);

			display: flex;
			align-items: center;

			@include min {
				width: calc((100% - 1rem) / 2);
				margin-right: rem(16);
			}

			@include min540 {
				padding: rem(28);
				margin-right: 0;
				width: 100%;
			}

			&:nth-child(3n) {
				margin-right: 0;

				@include min {
					margin-right: rem(16);
				}
			}

			&:nth-child(2n) {
				@include min {
					margin-right: 0;
				}
			}

			&:first-child, &:nth-child(2), &:nth-child(3) {
				margin-bottom: rem(32);

				@include min {
					margin-bottom: 0;
				}
			}

			@include min {
				&:first-child,
				&:nth-child(2),
				&:nth-child(3),
				&:nth-child(4) {
					margin-bottom: rem(16);
				}
			}

			@include min540 {
				margin-bottom: rem(16);
			}

			&__icon {
				margin-right: rem(16);

				@include min {
					max-width: rem(64);
				}


			}
			
			p {
				font-size: rem(20);
				font-weight: var(--s-medium);
				max-width: rem(215);

				@include min768 {
					font-size: rem(18);
				}
			}
		}
	}
}