.works {
	margin-bottom: var(--mb-section);

	@include min {
		margin-bottom: rem(72);
	}

	@include min540 {
		margin-bottom: rem(64);
	}

	.container {
		position: relative;
		
	}

	&__title {
		margin-bottom: rem(48);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@include min {
			margin-bottom: rem(40);
		}

		@include min360 {
			.title {
				font-size: rem(24);
			}
		}
	}

	.swiper-container {
		// overflow: visible;

		@include min540 {
			width: calc(100% - 40px);
		}

		.swiper-nav {
			position: absolute;
			top: 0;
			right: 0;
		}

		.swiper-pagination {
			margin-top: rem(12);
			bottom: auto;
			position: relative;
			align-items: center;
			// display: flex;
			flex-wrap: wrap;
			justify-content: center;

			display: none;

			@include min540 {
				display: flex;
			}

			.swiper-pagination-bullet {
				background-color: rgba(73, 122, 240, .5);
				width: rem(8);
				height: rem(8);
				opacity: 1;
			}

			.swiper-pagination-bullet-active {
				width: rem(12);
				height: rem(12);
				background-color: var(--c-blue);
			}
		}
	}

	.swiper-wrapper {
		height: rem(500);
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		align-content: flex-start;
		// justify-self: flex-start;

		@include min540 {
			height: rem(400);
		}
	}
	.swiper-slide {
		width: rem(160);
		height: rem(233);
		border-radius: rem(8);

		overflow: hidden;

		margin-right: rem(32);
		
		display: flex;
		flex-wrap: wrap;

		flex-direction: column;

		@include min540 {
			flex-wrap: unset;
			flex-direction: row;
			width: 100%;
			height: rem(400);

			.works__item {
				width: 100%;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		// .works__item {
		// 	width: rem(160);
		// 	height: rem(233);
		// 	margin-right: rem(32);

		// 	border-radius: rem(8);
		// 	overflow: hidden;

		// 	&:nth-child(3n-5) {
		// 		margin-bottom: rem(32);
		// 	}

		// 	&:nth-child(3n) {
		// 		width: rem(544);
		// 		height: 100%;
		// 	}

		// 	@include min540 {
		// 		width: 100% !important;
		// 	}
		// }

		&:nth-child(3n-5) {
			margin-bottom: rem(32);
		}

		&:nth-child(3n) {
			width: rem(544);
			height: 100%;

			.works__item {
				height: 100%;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			@include min {
				width: rem(448);
			}
		}
	}

	.swiper-slide-next {
		~ .swiper-slide {
			~.swiper-slide {
				~.swiper-slide {
					~.swiper-slide {
						opacity: .5;
					}
				}
			}
		}
	}
}

.swiper-nav {
	display: flex;
	flex-wrap: wrap;

	

	.swiper-prev,
	.swiper-next {
		width: rem(48);
		height: rem(48);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: var(--c-blue);

		transition: all 200ms ease;

		@include min540 {
			width: rem(38);
			height: rem(38);
		}

		&:hover {
			box-shadow: 0 0 20px rgba(30, 93, 244, .5);
		}

		i {
			color: white;
			font-size: rem(22);

			@include min540 {
				font-size: rem(16);
			}
		}
	}

	.swiper-prev {
		margin-right: rem(12);
		@include min540 {
			margin-right: rem(8);
		}
	}

	.swiper-next {
		i {
			margin-left: rem(4);
		}
	}

	.swiper-button-disabled {
		cursor: default;
		background-color: #8EACF5;

		&:hover {
			box-shadow: none;
		}
	}
}