.banner {
	margin-bottom: var(--mb-section);
	height: rem(400);

	@include min1050 {
		height: rem(300);
		margin-bottom: rem(176);
	}

	@include min540 {
		height: auto;
		margin-bottom: rem(28);
	}

	.container-max {
		position: relative;
	}

	&__image {
		overflow: hidden;
		border-radius: rem(16);
		height: rem(400);

		@include min1050 {
			height: rem(300);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__form {
		position: absolute;
		bottom: rem(-120);
		right: rem(40);
		width: rem(448);

		background-color: white;
		border-radius: rem(8);

		padding: rem(32);

		box-shadow: 0 0 rem(12) rgba(73, 122, 240, .4);

		@include min1050 {
			width: rem(331);
			right: 0;
			padding: rem(24);
			bottom: rem(-210);
		}

		@include min540 {
			position: relative;
			width: 100%;
			top: rem(-20);
			bottom: auto;
		}

		.title {
			margin-bottom: rem(24);
			font-size: rem(20);
			color: var(--c-text);
		}

		.field {
			margin-bottom: rem(16);
		}
	}
}