.article {

	&__content {
		margin-left: rem(96);
		margin-right: rem(96);

		> p {
			line-height: 136%;

			max-width: rem(832);

			margin-bottom: rem(22);

			&:last-of-type {
				margin-bottom: 0;
			}
		}

		h2 {
			font-size: rem(20);
			margin-top: rem(74);
			margin-bottom: rem(32);
		}
	}

	.image-main {
		border-radius: rem(16);
		margin-top: rem(40);
		// margin-bottom: rem(60);
	}

	.title {
		margin-bottom: rem(35);
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		margin: rem(40) 0;
		.item {
			position: relative;
			width: calc((100% - 4rem) / 3);

			margin-right: rem(32);
			border-radius: rem(16);
			height: rem(250);
			overflow: hidden;
			
			display: flex;
			align-items: center;
			justify-content: center;

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(0,0,0,.4);
			}

			b {
				position: relative;
				z-index: 1;
				font-size: rem(20);
				font-weight: bold;
				color: white;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				z-index: -1;
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&__items-list {
		position: relative;
		display: flex;
		flex-wrap: wrap;

		margin-top: rem(40);

		&::before {
			content: "";
			position: absolute;
			bottom: rem(-76);
			left: rem(56);
			width: rem(323);
			height: rem(270);
			background: url(../img/texts/decore.png) no-repeat;
			background-size: 100%;
		}

		.item {
			width: calc((100% - 4rem) / 3);
			margin-right: rem(32);
			margin-bottom: rem(40);
			padding-bottom: rem(32);

			border-radius: rem(16);
			background-color: white;
			box-shadow: 0 0 6px rgba(10, 31, 221, .25);
			overflow: hidden;

			display: flex;
			flex-direction: column;
			align-self: flex-start;

			&:last-child {
				margin-right: 0;
			}

			&__image {
				height: rem(250);
				margin-bottom: rem(20);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

			}

			&__title,
			&__list {
				margin-left: rem(24);
				margin-right: rem(24);
			}

			&__title {
				margin-bottom: rem(18);

				span {
					font-size: rem(20);
					color: var(--c-text);
					line-height: 136%;
					font-weight: var(--s-medium);
				}
			}

			&__list {
				li {
					position: relative;
					margin-bottom: rem(16);
					padding-left: rem(16);

					&::before {
						content: "";
						position: absolute;
						top: rem(7);
						left: 0;
						width: rem(6);
						height: rem(6);
						border-radius: 50%;
						background-color: var(--c-text);
					}
				}
			}

			&__price {
				margin-left: auto;
				margin-right: rem(24);
				
				b {
					font-size: rem(20);
				}
			}
		}
	}

}