.page_wrap {
	margin-top: rem(60);
	margin-bottom: rem(100);
}

.breadcrumbs {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(40);

	a, span {
		font-size: rem(16);
		color: var(--c-text);
	}

	a {
		position: relative;
		margin-right: rem(32);

		&::before {
			content: "/";
			pointer-events: none;
			position: absolute;
			top: 0;
			right: rem(-20);
		}

		&:hover {
			text-decoration: underline;
		}
	}


}

.product_items {
	display: flex;
	flex-wrap: wrap;

	.item {
		width: calc((100% - 4rem) / 3);
		margin-right: rem(32);
		margin-bottom: rem(40);
		padding-bottom: rem(32);

		border-radius: rem(16);
		background-color: white;
		box-shadow: 0 0 6px rgba(10,31,221,.25);
		overflow: hidden;

		display: flex;
		flex-direction: column;

		&:last-child {
			margin-right: 0;
		}

		&__image {
			height: rem(300);
			margin-bottom: rem(20);
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			
		}

		&__title, &__list {
			margin-left: rem(24);
			margin-right: rem(24);
		}

		&__title {
			margin-bottom: rem(18);

			span {
				font-size: rem(20);
				color: var(--c-text);
				line-height: 136%;
				font-weight: var(--s-medium);
			}
		}

		&__list {
			margin-bottom: rem(16);
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			span {
				color: var(--c-text);

				b {
					margin-right: rem(3);
				}

				&:last-child {
					color: black;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		> .flex {
			margin-top: auto;
		}
	}
}