.info {
	margin-bottom: var(--mb-section);

	@include min768 {
		margin-bottom: rem(72);
	}

	@include min540 {
		margin-bottom: rem(64);
	}

	.title {
		margin-bottom: rem(32);

		@include min540 {
			margin-bottom: rem(24);
		}
	}

	p {
		// font-size: rem(18);
		line-height: 146%;
		letter-spacing: rem(0.6);
		margin-bottom: rem(20);

		&:last-child {
			margin-bottom: 0;
		}
	}
}