.smart_menu {
	position: fixed;
	top: 0;
	padding-top: rem(75);
	padding-bottom: rem(75);
	left: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: #FAFBFF;
	z-index: 100;

	display: none;

	.container {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}

	// display: none;

	&__search {
		margin-top: rem(75);
		width: rem(410);

		margin-bottom: rem(40);

		@include min540 {
			width: 100%;
			margin-bottom: rem(32);
		}

		form {
			width: 100%;

			.field-set {
				position: relative;

				input {
					width: 100%;
					border-bottom: rem(1) solid #939393;
					padding: rem(8) 0;
					color: #BCBCBC;
					font-size: rem(24);
					font-family: var(--f-default);
					font-weight: var(--s-medium);

					@include min540 {
						font-size: rem(16);
					}

					@include placeholder {
						color: #BCBCBC;
					}
				}

				button {
					position: absolute;
					top: rem(5);
					right: 0;

					i {
						font-size: rem(28);

						@include min540 {
							font-size: rem(20);
						}
					}
				}
			}
		}
	}

	&__menu {
		width: 100%;
		margin-bottom: rem(20);

		ul {
			padding: rem(32) 0;
			border-bottom: rem(2) solid #E1E1E1;

			@include min540 {
				padding: rem(24) 0;
			}

			li {
				text-align: right;
				margin-bottom: rem(64);

				@include min540 {
					margin-bottom: rem(24);
				}

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(48);
					font-weight: var(--s-medium);
					color: var(--c-text);

					@include min540 {
						font-size: rem(24);
					}
				}
			}

			&:last-child {

				li {
					margin-bottom: rem(32);

					@include min540 {
						margin-bottom: rem(24);
					}

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(24);

						@include min540 {
							font-size: rem(18);
						}
					}
				}
			}
		}
	}

	&__bottom {
		.item {
			margin-bottom: rem(12);

			display: flex;
			align-items: center;
			justify-content: flex-end;

			i {
				margin-right: rem(10);
				font-size: rem(16);
			}

			&:last-child {
				display: none;
				margin-bottom: 0;
				font-size: rem(18);
				font-weight: var(--s-medium);
				text-align: right;
				span {
					
				}

				@include min540 {
					display: block;
				}
			}

			span {
				@include min540 {
					font-size: rem(14);
				}
			}
		}
	}
}

.smart_menu.active {
	display: block;
}

.list_menu {
	width: calc(100% - 40px);
	margin: 0 auto;
	margin-bottom: rem(32);

	// display: flex;
	flex-wrap: wrap;
	// justify-content: space-between;

	li {
		margin-right: rem(8);
		margin-bottom: rem(8);

		// &:nth-child(3n) {
		// 	margin-right: 0;
		// }

		a {
			border-radius: rem(8);
			font-size: rem(18);
			font-weight: var(--s-medium);
			color: black;
			padding: rem(12) rem(24);
			border: rem(1) solid #D4D4D4;
			display: block;
		}
	}
}