.stages {
	margin-bottom: rem(112);
	.title {
		margin-bottom: rem(60);
	}

	&__items {


		.item {
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(64);

			&:last-child {
				margin-bottom: 0;
			}

			b {
				max-width: rem(352);
				flex: 0 0 rem(352);
				margin-right: rem(22);

				font-size: rem(24);
				color: var(--c-text);
			}

			&__text {
				position: relative;
				// flex-grow: 1;
				span {
					position: absolute;
					top: rem(-80);
					left: 0;

					color: rgba(73,122,240,.15);
					font-size: rem(144);
					font-weight: var(--s-medium);
				}

				p {
					font-size: rem(16);
				}
			}
		}
	}
}