.contact {
	margin-bottom: rem(166);

	.container {
		position: relative;
	}

	&__map {
		border-radius: rem(16);
		overflow: hidden;

		height: rem(520);
	}

	.block_wrap {
		position: absolute;
		width: rem(288);
		left: rem(160);
		bottom: rem(-30);

		.title {
			margin-bottom: rem(20);
			font-size: rem(28);
			font-weight: var(--s-medium);
		}

		.item {
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(25);

			&:last-child {
				margin-bottom: 0;
			}

			.icon {
				width: rem(28);
				height: rem(28);
				flex: 0 0 rem(28);
				margin-right: rem(8);
			}

			.text {
				b {
					margin-bottom: rem(8);
					font-size: rem(20);
					font-weight: var(--s-medium);
					color: var(--c-text);
					display: block;
				}

				p, a {
					color: var(--c-text);
					font-size: rem(16);
				}
			}
		}

	}
}