.price {
	margin-bottom: var(--mb-section);

	@include min {
		margin-bottom: rem(72);
	}

	@include min540 {
		margin-bottom: rem(64);
	}

	h2.title {
		margin-bottom: rem(48);

		@include min {
			margin-bottom: rem(40);
		}
	}

	.desktop {
		@include min {
			display: none !important;
		}
	}

	.smart {
		display: none;

		@include min {
			display: flex;
			width: 100%;
			margin-bottom: rem(32);
		}

		@include min540 {
			margin-bottom: rem(16);
		}
	}

	&__content {
		display: flex;
		flex-wrap: wrap;

		> .block_wrap {
			width: rem(640);
			margin-right: rem(32);

			@include min {
				width: 100%;
				margin-right: 0;
				margin-bottom: rem(32);
			}

			@include min540 {
				margin-bottom: rem(16);
			}
		}

		.price__calc {
			width: rem(448);

			@include min {
				width: 100%;
				margin-right: 0;
			}

			.title, .subtitle {
				font-size: rem(20);
				font-weight: var(--s-medium);
				margin-bottom: rem(24);
				color: var(--c-text);
				display: block;
			}

			.subtitle {
				margin-top: rem(24);
				margin-bottom: 0;
			}

			.custom-select {
				margin-bottom: rem(16);
			}

			.button-border {
				margin-top: rem(32);
				display: inline-block;

				padding: rem(16) rem(32);
				font-size: rem(20);
			}
		}
	}
}