.header {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(40);

	@include min1050 {
		position: relative;
		z-index: 101;
		background-color: #FAFBFF;
		margin-bottom: 0;
	}

	

	// @include min540 {
	// 	margin-bottom: rem(20);
	// }

	&__top {
		padding: rem(12) 0;
		margin-bottom: rem(20);

		@include min1050 {
			display: none;
		}

		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		&-left {
			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					position: relative;
					margin-right: rem(40);

					&:last-child {
						margin-right: 0;

						&::before {
							display: none;
						}
					}

					&::before {
						content: "";
						position: absolute;
						top: 0;
						right: rem(-20);
						height: rem(20);
						width: rem(1);
						background-color: #C4C4C4;
					}
					a {
						color: var(--c-text);
						font-weight: var(--s-medium);
					}
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.item {
				i {
					margin-right: rem(10);
					font-size: rem(14);
				}
				span {
					color: #2A2A2A;
				}
				&:first-child {
					margin-right: rem(30);
				}

				&:last-child {
					margin-left: rem(80);

					i {
						font-size: rem(20);
						margin-right: 0;
					}
				}
			}

			
		}
	}

	&__center {
		margin-bottom: rem(24);

		@include min1050 {
			margin-top: rem(32);
			margin-bottom: rem(32);
			background-color: #FAFBFF;
		}
		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		&-phone {

			@include min1050 {
				margin-left: auto;
				margin-right: rem(24);
			}

			.text {
				margin-right: rem(40);
				font-size: rem(20);
				color: #2A2A2A;
				font-weight: var(--s-medium);

				@include min1050 {
					margin-right: rem(16);
				}

				@include min660 {
					display: none;
				}
			}

			.button-blue {
				@include min1050 {
					padding: rem(15);
				}
				span {
					@include min1050 {
						display: none;
					}
				}
				i {
					display: none;

					@include min1050 {
						display: block;
						font-size: rem(20);
					}
				}
			}
		}

		&-burger {
			display: none;

			@include min1050 {
				display: block;

				.button-blue {
					display: flex;
					align-items: center;
					padding: rem(14) rem(24);

					> span {
						margin-right: rem(8);
					}
				}

				.burger {
					width: rem(25);
					height: rem(18);
					display: flex;
					flex-direction: column;
					justify-content: space-between;

					span {
						width: 100%;
						height: rem(2);
						background-color: white;
						transition: all 200ms ease;
					}

					&::after, &::before {
						content: "";
						width: 100%;
						height: rem(2);
						background-color: white;
						transition: all 200ms ease;
					}
				}
			}

			@include min660 {
				
				.button-blue {
					width: rem(52);
					height: rem(52);
					padding: 0;
					display: flex;
					justify-content: center;
					> span {
						display: none;
					}
				}
					
			}
			// }
		}

		&-burger.active {
			@include min1050 {
				.burger {
					span {
						opacity: 0;
					}

					&::before {
						transform: rotate(45deg) translateY(rem(7)) translateX(rem(5));
					}

					&::after {
						transform: rotate(-45deg) translateY(rem(-6)) translateX(rem(4));
					}
				}
			}
		}
	}

	&__nav {
		width: 100%;
		border-radius: rem(16);
		
		background-color: white;
		box-shadow: 0 0 rem(10) rgba(10,31,221,.25);

		@include min1050 {
			display: none;
		}

		> ul {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			// height: 100%;

			> li {
				position: relative;
				flex-grow: 1;
				position: relative;

				// &:hover {
				// 	ul {
				// 		display: flex;
				// 	}
				// }

				ul {
					position: absolute;
					top: rem(64);
					left: 0;
					padding: rem(24);
					background-color: white;
					border-radius: rem(8);
					box-shadow: 0 0 12px rgba(73, 122, 240, .4);
					z-index: 10;

					// display: flex;
					flex-wrap: wrap;

					min-width: rem(433);

					display: none;

					li {
						margin-bottom: rem(16);

						&:nth-child(2n-1) {
							width: rem(218);
						}

						&:last-child, &:nth-last-child(2) {
							margin-bottom: 0;
						}

						a {
							&:hover {
								color: var(--c-blue);
							}
						}
					}
				}

				ul.active {
					display: flex;
				}

				&:first-child {
					a {
						border-top-left-radius: rem(16);
						border-bottom-left-radius: rem(16);
					}
				}

				&:last-child {

					a {
						border-top-right-radius: rem(16);
						border-bottom-right-radius: rem(16);
					}

					&::before {
						display: none;
					}
				}

				&::before {
					content: "";
					position: absolute;
					top: rem(15);
					right: 0;
					height: rem(24);
					width: rem(1);
					background-color: #C4C4C4;
				}
				> a {
					display: block;
					text-align: center;
					padding: rem(16) 0;
					font-size: rem(20);
					font-weight: var(--s-medium);
					color: var(--c-text);

					transition: all 200ms ease;

					&:hover {
						background-color: #F3F7FF;
						color: #2B63E8;
					}
				}
			}
		}
	}
}

.header_fixed {
	@include min1050 {
		background-color: #FAFBFF;

	
		.header__center {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			background-color: #FAFBFF;
			padding: rem(32) 0;
			margin: 0;
		}

		&::before {
			content: "";
			width: 100%;
			height: rem(118);
		}
	}

	
}

.list_menu {
	display: none;

	@include min {
		display: flex;
	}
}