.reviews {
	margin-bottom: rem(100);

	@include min {
		margin-bottom: rem(148);
	}

	// @include min540 {
	// 	margin-bottom: rem(64);
	// }

	.swiper-container {
		padding: rem(50) rem(20);
		margin: rem(-50) rem(-20);
	}

	&__title {
		margin-bottom: rem(68);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	&__item {
		position: relative;
		border-radius: rem(8);
		padding: rem(24);
		box-shadow: 0 0 6px rgba(73, 122, 240, .25);
		background-color: white;

		&::before {
			content: "";
			position: absolute;
			top: rem(-45);
			left: rem(44);
			background: url(../img/quote.png);
			background-size: 100%;
			width: rem(95);
			height: rem(63);
			z-index: -1;
		}

		.title {
			display: block;
			font-size: rem(20);
			font-weight: var(--s-medium);
			margin-bottom: rem(16);

			@include min {
				font-size: rem(18);
			}
		}

		p {
			line-height: 136%;
			font-size: rem(16);
			@include min {
				font-size: rem(14);
			}
			a {
				color: #497AF0;
				margin-left: rem(3);

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	.button-border {
		margin-top: rem(32);
		margin-left: auto;
		margin-right: auto;
		display: inline-block;
	}
}