.footer {
	padding: rem(50) 0;
	background: linear-gradient(180deg, rgba(234, 228, 255, 0) 0%, #EAE4FF 47.45%);

	&__top {
		margin-bottom: rem(40);
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		ul {
			@include min {
				width: 33.333%;
				margin-bottom: rem(44);
			}
			@include min540 {
				width: 50%;
				margin-bottom: rem(22);
			}
			li {

				margin-bottom: rem(20);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(18);
					color: var(--c-text);

					@include min540 {
						font-size: rem(16);
					}
				}
			}
		}

		&-phone {
			display: flex;
			flex-direction: column;
			align-items: flex-end;

			@include min {
				width: 100%;
				justify-content: center;
				flex-direction: row;
				align-items: center;
			}

			@include min540 {
				width: 50%;
				flex-direction: column;
			}

			.text {
				margin-bottom: rem(24);
				margin-right: rem(14);
				font-size: rem(24);
				color: #2A2A2A;
				font-weight: var(--s-medium);

				@include min {
					margin-bottom: 0;
				}

				@include min540 {
					font-size: rem(16);
					margin-bottom: rem(16);
				}

				@include min340 {
					font-size: rem(14);
				}
			}

			.button-blue {
				padding: rem(16) rem(32);

				@include min540 {
					font-size: rem(15);
					padding: rem(8) rem(10);

				}

				@include min340 {
					font-size: rem(14);
					padding: rem(8);
				}
			}
		}
	}
}